import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, formValueSelector, Field } from "redux-form";
import BootstrapTable from "react-bootstrap-table-next";
import {
  getValidasiGiro,
  getValidasiGiroSelected,
} from "../../actions/validasiPembayaranGiro";
import { renderFieldSelect } from "../../renderField";

class FormValidasiPembayaranGiro extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "rek_asal",
          text: "Rekening Asal",
        },
        {
          dataField: "rek_tujuan",
          text: "Rekening Tujuan",
        },
        {
          dataField: "saldo_rp",
          text: "Saldo RP",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  className="btn btn-success mr-2"
                  data-toggle="modal"
                  data-target="#primary-header-modal"
                  onClick={() =>
                    this.props.dispatch(getValidasiGiroSelected(row))
                  }
                >
                  <i className="fa fa-check-circle mr-1"></i>
                  VALIDASI
                </button>
              </div>
            );
          },
        },
      ],
      searchText: "",
    };
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }
  componentDidMount() {
    this.props.dispatch(getValidasiGiro("K"));
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  searchList() {
    this.props.dispatch(getValidasiGiro(this.props.jenis_giro));
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="jenis_giro"
              component={renderFieldSelect}
              options={[
                {
                  value: "K",
                  name: "KREDIT",
                },
                {
                  value: "D",
                  name: "DEBIT",
                },
              ]}
              type="text"
              label="Jenis Transaksi"
              placeholder="Masukan Jenis Transaksi"
            />
          </div>

          <div className="col-lg-1">
            <div className="form-group">
              <label htmlFor="" className="text-white">
                .
              </label>
              <button
                type="button"
                className="btn btn-primary form-control"
                onClick={() => this.searchList()}
              >
                Cari
              </button>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <BootstrapTable
              keyField="noTransaksi"
              data={this.props.listValidasiGiro || []}
              columns={this.state.columns}
              bordered={true}
              headerClasses="header-table"
            />
          </div>
        </div>
      </form>
    );
  }
}

FormValidasiPembayaranGiro = reduxForm({
  form: "FormValidasiPembayaranGiro",
  enableReinitialize: true,
})(FormValidasiPembayaranGiro);
const selector = formValueSelector("FormValidasiPembayaranGiro"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, jenis_giro } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "jenis_giro"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    listValidasiGiro: state.provinsi.listValidasiGiro,
    rekening: state.provinsi.rekening,
    jenis_giro: jenis_giro,
  };
})(FormValidasiPembayaranGiro);
